/* contatcForm ----------------------------- */
.contactForm fieldset {
  margin: 0;
  padding: 0;
  border: none;
}
.contactForm input[type=text], .contactForm textarea, .contactForm .selectBox, .contactForm .boxDefault, .contactForm label.inputFile, .contactForm label.time, .contactForm label.date {
  width: 100%;
  height: 40px;
  padding: 0px 0.7em;
  outline: none;
  display: block;
  position: relative;
  z-index: 20;
  display: block;
  color: #fff;
  font-size: 14px;
  line-height: 2.5;
  font-family: 1;
  font-weight: 400;
  text-align: left;
  border-width: 1px;
  border-style: solid;
  margin-bottom: 1em;
  background: transparent;
}
.contactForm label {
  display: block;
  color: #fff;
  font-size: 16px;
  line-height: 2.5;
  font-family: 1;
  font-weight: 400;
  text-align: left;
  margin-bottom: 0;
}
.contactForm label em {
  float: left;
  margin-left: 1em;
  line-height: 32px;
}
.contactForm label input {
  float: left;
}
.contactForm label.selectBox {
  position: relative;
  width: 100%;
  padding: 0;
}
.contactForm label.selectBox select {
  position: relative;
  z-index: 10;
  width: 100%;
  padding: 0px 1em;
  border: none;
  display: block;
  color: #816434;
  font-size: 14px;
  line-height: 2.5;
  font-family: 1;
  font-weight: 400;
  text-align: left;
  background-color: transparent;
  -webkit-appearance: none !important;
  -moz-appearance: none !important;
  appearance: none !important;
}
.contactForm label.selectBox option {
  background-color: #fff;
  color: #816434;
}
.contactForm label.date {
  position: relative;
  width: 100%;
  padding: 0;
  border: 1px solid #7e7d7e;
}
.contactForm label.date input[type=date] {
  outline: none;
  position: relative;
  z-index: 10;
  width: calc(100% - 1em);
  height: 2.4em;
  padding: 0px 1em;
  margin-bottom: 1em;
  border: none;
}
.contactForm label.date::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 100;
  top: 50%;
  right: 1em;
  -moz-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 18px;
  height: 16px;
  background-image: url("../img/icone/date.png");
}
.contactForm label.time {
  position: relative;
  width: 100%;
  padding: 0;
}
.contactForm label.time input[type=time] {
  outline: none;
  position: relative;
  z-index: 10;
  width: 100%;
  padding: 0px 1em;
  margin-bottom: 1em;
}
.contactForm label.time::before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  top: 50%;
  right: 1em;
  -moz-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  -o-transform: translateY(-50%);
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  width: 5px;
  height: 20px;
  background-image: url("../img/icone/select.png");
}
.contactForm label.inputFile {
  position: relative;
  width: 100%;
  padding: 0;
  border: 1px solid #7e7d7e;
}
.contactForm label.inputFile input[type=file] {
  outline: none;
  position: relative;
  z-index: 10;
  width: 100%;
  height: 2.4em;
  padding: 0px 1em;
  margin-bottom: 1em;
  border: none !important;
  opacity: 0;
  cursor: pointer;
}
.contactForm label.inputFile em {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  font-style: normal;
  cursor: pointer;
  display: block;
  color: #816434;
  font-size: 14px;
  line-height: 2.7;
  font-family: "Ubuntu", Helvetica, Arial, sans-serif;
  font-weight: 400;
  text-align: left;
}
.contactForm label.checkBox {
  width: 100%;
  overflow: hidden;
}
.contactForm label.checkBox input[type=checkbox] {
  display: inline-block;
}
.contactForm label.checkBox em {
  display: block;
  width: calc(100% - 1.5em);
  float: right;
  margin-bottom: 1em;
  margin-left: 0;
  line-height: 1;
  font-style: normal;
}
.contactForm input[type=text] {
  line-height: 32px;
}
.contactForm input[name=randomfield] {
  width: 70px;
  margin-bottom: 0;
}
.contactForm textarea {
  height: 10.5em;
  resize: none;
  line-height: 1.5;
  padding-top: 0.5em;
}
.contactForm textarea.msnServico {
  height: 6em;
}
.contactForm input[type=submit] {
  width: 100%;
  font-size: 14px;
  text-align: center;
  overflow: hidden;
  cursor: pointer;
  color: #fff;
  background-color: #0C3A79;
}
.contactForm ::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #fff;
}
.contactForm ::-moz-placeholder { /* Firefox 19+ */
  color: #fff;
}
.contactForm :-ms-input-placeholder { /* IE 10+ */
  color: #fff;
}
.contactForm :-moz-placeholder { /* Firefox 18- */
  color: #fff;
}

.owl-carousel#cliente {
